.login-container-grid {
  display: grid;
  height: 100vh;
  align-content: center;
  background-image: url("../../images/new-york-1.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-size: cover; }
  .login-container-grid .grid-container {
    display: grid;
    grid-template-columns: 350px;
    margin: auto auto;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.4); }
  .login-container-grid .item {
    margin: 10px; }
    .login-container-grid .item:last-child {
      margin-bottom: 15px; }
  .login-container-grid .title {
    color: #fff; }
  .login-container-grid .subgrid {
    display: grid;
    grid-template-columns: 50% 50%;
    justify-content: center;
    grid-column-gap: 10px; }
    .login-container-grid .subgrid--item {
      margin: 0; }
  .login-container-grid .btn {
    margin: 0 8px; }

